.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    color: #383838;

    .close-button {
        border-width: 0;
        color: rgb(177, 77, 77);
        cursor: pointer;
        transition: filter 0.3s ease-in-out;
        position: absolute;
        left: 0;
        bottom: 0;
        transform: translate(10%, -10%);
        &:hover {
            filter: contrast(120%);
        }
    }
}

.modal-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    max-width: 400px;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
