.first-page-movie-wrapper {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-image: $background;
  background-position: center center;
  background-size: cover;
  padding: 10px 3rem;

  .info {
    width: 40%;

    &__main-header {
      font-size: 54px;
      font-weight: 900;
      text-shadow: 2px 2px 2px rgb(30, 30, 30);
      color: whitesmoke;
      text-align: left;
      width: 100%;
      text-shadow: 2px 2px 1px gray;
    }

    &__cecoundary-header {
      text-align: left;
      font-size: 18px;
      opacity: 0.7;
    }
  }

  img {
    object-position: center center;
    object-fit: contain;
    width: 100%;
    height: 500px;
    border-radius: 40px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border: 2px solid rgba(0, 0, 0, 0.166);
    box-shadow:
      rgba(0, 0, 0, 0.25) 0px 54px 55px,
      rgba(0, 0, 0, 0.12) 0px -12px 30px,
      rgba(0, 0, 0, 0.12) 0px 4px 6px,
      rgba(0, 0, 0, 0.17) 0px 12px 13px,
      rgba(0, 0, 0, 0.09) 0px -3px 5px;
    -webkit-mask-image: linear-gradient(to left, black 20%, transparent 95%);
    mask-image: linear-gradient(to left, black 20%, transparent 95%);
  }
}

@media screen and (max-width: 768px) {
  .first-page-movie-wrapper {
    flex-wrap: wrap;
    flex-direction: column;
    
    .info {
      width: 100%;
    }
    
    img {
      height: fit-content;
    }
  }
}
